.textfieldWrapper {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  width: 100%; }
  .textfieldWrapper .textfieldInputWrapper {
    display: flex;
    flex-direction: column-reverse;
    position: relative;
    width: 100%; }
    .textfieldWrapper .textfieldInputWrapper.currency:before {
      content: "$";
      position: absolute;
      bottom: 10px;
      left: 10px;
      color: #8a96a0; }
    .textfieldWrapper .textfieldInputWrapper.currency.textfieldError:before {
      bottom: 28px; }

input[type=number].currency, input[type=text].currency {
  padding-left: 25px; }

input[type=text], input[type=password], input[type=email], input[type=number] {
  border: 1px solid #8b8b8b;
  border-radius: 3.8px;
  padding: 10px;
  font-size: 14px; }
  input[type=text]:focus, input[type=password]:focus, input[type=email]:focus, input[type=number]:focus {
    border: 1px solid #3faca7; }

.textfieldWrapper:not(.textfieldError) input[type=text]:focus + label, .textfieldWrapper:not(.textfieldError) input[type=password]:focus + label, .textfieldWrapper:not(.textfieldError) input[type=email]:focus + label, .textfieldWrapper:not(.textfieldError) input[type=number]:focus + label {
  color: #3faca7; }

label {
  text-transform: uppercase;
  letter-spacing: 1.6px;
  color: #373835;
  font-size: 11px;
  margin-bottom: 10px; }

.textfieldErrorMsg {
  display: none; }

.textfieldError .textfieldErrorMsg {
  display: block;
  margin-top: 3px;
  font-size: 12px;
  font-weight: 300;
  color: #de3535; }

.textfieldError input[type=text], .textfieldError input[type=password], .textfieldError input[type=email], .textfieldError input[type=number] {
  border-color: #de3535; }

.textfieldError label {
  color: #de3535; }

.textfieldError:focus + label {
  color: #de3535 !important; }
