.card {
  padding: 40px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  width: 700px; }
  .card h2 {
    margin: 0;
    font-weight: 500; }
  .card .body {
    margin-top: 40px; }
  @media (max-width: 1024px) {
    .card {
      width: 80%; } }
  @media (max-width: 768px) {
    .card {
      width: 100%;
      margin: 0 30px; } }
  @media (max-width: 440px) {
    .card {
      width: 100%;
      padding: 40px 20px;
      margin: 0; } }
