.button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  color: #00897b;
  width: 200px;
  height: 48px;
  border-radius: 2px;
  border: 1px solid #00897b;
  background-color: #fff; }
  .button.loading {
    padding: 0 25px; }
  .button img {
    width: 33px;
    height: 33px; }
  .button:hover {
    cursor: pointer; }
  .button.primary {
    color: #fff;
    background-color: #00897b;
    box-shadow: 4px 4px 18px 0 rgba(55, 56, 53, 0.25);
    border: 1px solid #00897b; }
  .button.disabled {
    color: #fff;
    background: #8a96a0;
    border-color: #8a96a0;
    cursor: auto; }
